.apartments {
  max-width: $vp-container-desktop;
  position: relative;
  padding: 24px 0;
}

.apartments__title {
  margin: 0;
  padding: 0;
  color: $color-default-black;
  font-size: $fz-title-big;
  line-height: $lh-title-big;
  font-weight: $fw-title-big;
  text-transform: uppercase;

  @include vp-767 {
    font-size: $fz-title;
    line-height: $lh-title;
    font-weight: $fw-title;
  }
}

.apartments__link {
  color: $color-default-white;

  @include hover-focus {
    text-decoration-line: underline;
  }

  &:active {
    color: rgba(255, 255, 255, 0.50);
  }
}

.apartments__subtitle {
  margin: 0;
  font-size: $fz-subtitle-big;
  line-height: $lh-subtitle-big;
  font-weight: $fw-subtitle-big;
  cursor: pointer;

  @include vp-767 {
    font-size: $fz-subtitle;
    line-height: $lh-subtitle;
    font-weight: $fw-subtitle;
  }

}

.apartments__description {
  max-width: 322px;
  margin: 0;
  margin-top: 10px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.20);
  font-family: $ff-sans;
  font-size: $fz-middle;
  line-height: $lh-middle;

  @include vp-767 {
    font-size: $fz-default;
    line-height: $lh-default;
  }
}

.apartments__caption {
  margin: 0;
  font-family: $ff-sans;
  font-size: $fz-middle;
  line-height: $lh-middle;
  font-weight: 700;
  max-width: 117px;
  margin-top: 17px;

  @include vp-767 {
    font-size: $fz-default;
    line-height: $lh-default;
    font-weight: 700;
  }
}

.apartments__price {
  color: $color-primary-2;
  font-size: $fz-price-big;
  line-height: $lh-price-big;
  font-weight: 700;

  @include vp-767 {
    font-size: $fz-price;
    line-height: $fz-price;
    font-weight: 700;
  }

  &::after {
    content: "₽";
    font-family: $ff-sans;
    font-size: 20px;
    font-weight: 400;
    line-height: 32px;
    color: rgba(255, 255, 255, 0.60);
    margin-left: 6px;
  }

  &::before {
    font-family: $ff-roboto;
    content: "от";
    margin-right: 6px;
    font-size: 17px;
    font-weight: 700;
    line-height: 24px;
    color: #fff;
  }
}

.apartments__button {
  display: block;
  margin-left: auto;
  font-family: $ff-sans;
  color: $color-default-white;
  background-color: $color-primary-1;
  border: none;
  border-radius: 5px;
  font-size: $fz-button-big;
  line-height: $lh-button-big;
  font-weight: 700;
  text-align: center;
  width: 180px;
  height: 48px;
  align-self: flex-end;
  cursor: pointer;
  transition: $trans-600;

  @include vp-1279 {
    width: 288px;
    height: 48px;
    margin-top: 16px;
  }

  @include vp-767 {
    font-size: $fz-button;
    line-height: $lh-button;
    font-weight: 700;
  }

  @include hover {
    background-color: $color-primary-3;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  }

  &:active {
    background-color: $color-primary-3;
    color: #F9637C;
  }
}

.apartments__list {
  margin: 0;
  margin-top: 33px;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 32px;
  min-width: $vp-container-desktop;

  @include vp-1279 {
    min-width: $vp-container-tablet;
    gap: 28px;
    justify-content: center;
  }

  @include vp-767 {
    min-width: $vp-container-mobile;
    gap: 32px;
    justify-content: center;
  }
}

.apartments__item {
  position: relative;
  box-sizing: border-box;
  display: flex;
  align-content: flex-end;
  flex-wrap: wrap;
  text-align: start;
  color: $color-default-white;
  width: 372px;
  font-weight: 400;
  padding: 167px 24px 22px 24px;
  background: $color-gradient;
  border-radius: 10px;
  transition: $trans-600;

  @include hover {
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.60);

    .apartments__decoration {
      display: block;
    }
  }

  @include vp-1279 {
    padding: 150px 24px 24px 24px;
    width: 336px;

    &:last-child {
      margin-top: 9px;
    }
  }

  @include vp-767 {
    padding: 77px 21px 21px 21px;
    width: 288px;

    &:last-child {
      margin-top: 0;
    }
  }

}

.apartments__item--disabled {
  position: relative;
  background: rgba(171, 169, 169, 0.595);
}

.apartments__image {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
  }
}

.apartments__decoration {
  position: absolute;
  top: 24px;
  color: #FFF;
  font-family: $ff-roboto;
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
  padding: 4px 4px 5px 16px;
  border-radius: 3px 0 0 3px;
  background-color: #FF7647;
  display: none;


  &::after {
    top: 0;
    left: 100%;
    position: absolute;
    display: block;
    height: 30px;
    width: 36px;
    content: "";
    background-image: url("/img/svg/icon-discount.svg");
  }
}
